@import "variables.scss";

@font-face {
  font-family: 'ProximaNova';
  src: url('../fonts/ProximaNova-Regular.eot');
  src: url('../fonts/ProximaNova-Regular.eot?#iefix') format('embedded-opentype'),
  url('../fonts/ProximaNova-Regular.woff') format('woff'),
  url('../fonts/ProximaNova-Regular.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'ProximaNova';
  src: url('../fonts/ProximaNova-Semibold.eot');
  src: url('../fonts/ProximaNova-Semibold.eot?#iefix') format('embedded-opentype'),
  url('../fonts/ProximaNova-Semibold.woff') format('woff'),
  url('../fonts/ProximaNova-Semibold.ttf') format('truetype');
  font-weight: 600;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
}


body {
  min-width: 320px;

  font-family: $font-family;
  //background: $bg;

  &.overfl {
    overflow: hidden;
    height: 100vh;
  }
}

.start-page {
  padding: 30px;

  &-logo {
    display: block;
    margin-bottom: 30px;
  }

  ul {
    display: inline-block;
    vertical-align: top;
    padding-right: 60px;

    li {
      padding: 5px 0;

      b {
        font-size: 20px;
      }

      a {
        font-size: 16px;
        line-height: 140%;
        color: $black;

        &:hover {

        }
      }
    }
  }
}

div,
span,
a,
input,
textarea,
select,
button,
.button {
  &:focus {
    outline: none;
  }
}

svg {
  use {
    transition: 0.3s;
  }
}

ul {
  li {
    list-style: none;
  }
}

a {
  cursor: pointer;
  transition: 0.3s;

  &:hover {
    text-decoration: none;
  }
}

.button {
  border: none;
  box-shadow: none;
  outline: none;
  transition: 0.3s;
  text-align: center;
  justify-content: center;

  &-bord {
    padding: 11px 24px;
    display: inline-block;

    border: 2px solid $black;
    border-radius: 10px;
    font-size: 16px;
    line-height: 19px;
    color: $black;

    &:hover {
      background: $black;
      color: $white;
    }
  }

  &-bord2 {
    display: inline-flex;
    align-items: center;
    padding: 11px 15px;

    font-size: 16px;
    line-height: 19px;
    color: $black;

    border: 2px solid $black;
    border-radius: 10px;

    img {
      margin-left: 5px;
    }

    &:hover {
      color: $white;
      background: $black;
    }
  }

  &-all {
    display: inline-flex;
    align-items: center;
    padding: 11px 15px;

    letter-spacing: 0.07em;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 13px;
    line-height: 121.03%;
    color: $black3;

    border: 1px solid $green;
    border-radius: 10px;

    img {
      margin-left: 5px;
    }

    &:hover {
      color: $black3;
      background: $green;
    }
  }

  &-primary {
    padding: 11px 24px;
    display: inline-flex;
    align-items: center;

    border-radius: 10px;
    background: $green;
    font-weight: 500;
    font-size: 13px;
    line-height: 121.03%;
    color: $white!important;
    letter-spacing: 0.07em;
    text-transform: uppercase;

    img {
      margin-left: 5px;
    }

    &:hover {
      background: $green-hov;
    }
  }

  &-secondary {
    display: inline-flex;
    align-items: center;
    padding: 11px 24px;

    font-weight: 500;
    font-size: 13px;
    line-height: 121.03%;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    color: $white;
    background: $green;
    border-radius: 10px;

    &:hover {
      background: $green-hov;
      color: $white;
    }
  }

  &-gr {
    padding: 11px 24px;
    display: inline-flex;
    align-items: center;

    color: $green-hov;
    background: $green6;
    font-weight: 500;
    font-size: 13px;
    line-height: 121.03%;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    border-radius: 10px;

    &:hover {
      color: $white;
      background: $green-hov;
    }
  }

  &-tx {
    padding: 11px 24px;
    display: inline-flex;

    font-weight: 500;
    font-size: 13px;
    line-height: 121.03%;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    color: $black3;

    &:hover {
      color: $green-hov;
    }
  }
}

.container {
  max-width: 100%;

  padding-right: 130px;
  padding-left: 130px;
}

@media screen and (max-width: 1200px) and (min-width: 1500px) {
  .col-lg-3 {
    .catalog-min & {
      flex: 0 0 33.333%;
      max-width: 33.333%;
    }
  }
}

.col-lg-2-1 {
  margin-bottom: 30px;

  @media (min-width: 1500px) {
    flex: 0 0 20%;
    max-width: 20%;
  }
}


h2 {
  margin-bottom: 0;

  font-weight: 500;
  font-size: 26px;
  line-height: 130%;
  color: $black;
}

h1 {
  margin-bottom: 0;
  margin-right: 8px;

  display: inline-block;
  vertical-align: bottom;

  font-weight: 500;
  font-size: 29px;
  line-height: 32px;
  color: $black;
}

h3 {
  margin-bottom: 30px;

  font-weight: 500;
  font-size: 20px;
  line-height: 130%;
  color: $black;
}

.wrapper {
  padding-left: 315px;

  &__inner {
    padding-left: 50px;
  }
}

.header {
  position: relative;

  border-bottom: 1px solid $bord;

  &__top {
    padding: 6px 0;

    background: $black2;

    &-inner {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &-info {
      display: flex;
      align-items: center;
    }

    &-menu {
      ul {
        display: flex;
        align-items: center;
        margin-bottom: 0;

        li {
          margin-left: 34px;

          a {
            font-size: 14px;
            line-height: 16px;
            color: $white;

            &:hover {
              color: $green;
            }
          }
        }
      }
    }
  }

  &__city {
    display: flex;
    align-items: center;
    margin-right: 52px;

    font-weight: 500;
    font-size: 13px;
    line-height: 121.03%;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    color: $white;

    img {
      margin-right: 12px;
    }
  }

  &__tel {
    font-size: 14px;
    line-height: 16px;
    color: $gray;

    a {
      margin-left: 10px;

      font-weight: 500;
      font-size: 15px;
      line-height: 121.03%;
      color: $green;

      &:hover {
        color: $green-hov;
      }
    }
  }

  &__bottom {
    padding: 17px 0;

    background: $bg2;
    border-bottom: 1px solid $bord;

    &-inner {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &-info {
      display: flex;
      align-items: center;

      &-item {
        margin-left: 37px;

        position: relative;

        font-weight: 500;
        font-size: 13px;
        line-height: 121.03%;
        letter-spacing: 0.07em;
        text-transform: uppercase;
        color: $black3;

        &-cart {
          .header__bottom-info-notif {
            left: 17px;
          }
        }

        img {
          height: 29px;
          margin-right: 10px;
          margin-bottom: 5px;
        }

        &:hover {
          color: $orange;
        }
      }

      &-notif {
        width: 17px;
        height: 17px;
        display: flex;
        align-items: center;
        justify-content: center;

        position: absolute;
        left: 26px;
        top: 0;

        background: $green-hov;
        border-radius: 100px;
        font-weight: 700;
        font-size: 10px;
        line-height: 121.03%;
        color: $white;
      }
    }
  }

  &__search {
    width: 52%;

    position: relative;

    input {
      width: 100%;
      height: 46px;
      padding: 10px 55px;

      border-radius: 14px;
      background: $gray2;
      font-size: 16px;
      line-height: 19px;
      color: $black2;
      border: none;
      box-shadow: none;
      background: $gray2;

      &::-webkit-input-placeholder { color: $gray3; }
      &:-ms-input-placeholder { color: $gray3; }
      &::placeholder { color: $gray3; }
    }

    &-btn {
      width: 24px;
      height: 29px;

      border: none;
      background: transparent;

      position: absolute;
      top: 0;
      bottom: 0;
      left: 18px;
      margin: auto;
      z-index: 2;

      cursor: pointer;
    }

    &-clean {
      width: 12px;
      height: 12px;

      position: absolute;
      top: -11px;
      bottom: 0;
      right: 19px;
      margin: auto;
      z-index: 2;
      cursor: pointer;
    }
  }

  &__toggler {
    display: none;
  }

  &__logo {
    &-mob {
      display: none;
    }
  }

  &__menu {
    &-mob {
      display: none;
    }
  }
}

.logo {
  display: flex;
  align-items: center;
  margin-bottom: 38px;

  font-weight: 700;
  font-size: 13px;
  line-height: 160.03%;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $green-hov!important;

  img {
    margin-right: 15px;
  }

  span {
    position: relative;
    top: 12px;
  }
}

.menu {
  width: 315px;
  height: 100vh;
  padding: 12px 36px;
  min-height: 100%;

  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;

  background: $white;
  box-shadow: 1px 4px 13px rgba(173, 168, 154, 0.12);

  &__top {
    display: none;
  }

  ul {
    & > li {
      border-top: 1px solid $bord;

      & > a {
        display: block;
        padding: 18px 0;

        font-weight: 500;
        font-size: 13px;
        line-height: 121.03%;
        letter-spacing: 0.07em;
        text-transform: uppercase;
        color: $black3;

        background-image: url("../images/svg/arrow2.svg");
        background-size: 7px 11px;
        background-repeat: no-repeat;
        background-position: center right;

        &:hover {
          color: $green-hov;
        }
      }

      @media screen and(min-width: 991px) {
        &:hover {
          & > .menu__submenu {
            display: block;
          }
        }
      }
    }
  }

  &__submenu {
    display: none;
    padding: 55px 182px 30px 82px;
    width: calc(100% - 279px);
    height: 100vh;
    min-height: 100%;
    overflow: auto;

    position: fixed;
    top: 0;
    left: 279px;
    z-index: 100;

    background: $white;
    //column-count: 2;
    //column-gap: 20px;

    &-list {
      float: left;
      width: 48%;

      margin-bottom: 30px;
    }

    &-logo {
      width: 130px;
      padding: 55px 0 30px;

      position: absolute;
      right: 20px;
      top: 0;

      text-align: center;

      img{
        display: block;
        margin-bottom: 16px;
      }
    }

    &-title {
      display: block;
      margin-bottom: 12px;

      font-weight: 500;
      font-size: 13px;
      line-height: 121.03%;
      letter-spacing: 0.07em;
      text-transform: uppercase;
      color: $black3;

      &:hover {
        color: $green-hov;
      }
    }

    ul {
      display: inline-block;

      li {
        margin-bottom: 15px;
        border: none;

        &:last-of-type {
          margin-bottom: 0;
        }

        a {
          padding: 0!important;

          text-transform: inherit;
          font-size: 14px;
          line-height: 16px;
          color: $gray11;
          background: transparent;
          font-weight: 400;
          letter-spacing: 0;

          &:hover {
            color: $green-hov;
          }
        }
      }
    }
  }
}

.banner {
  padding: 45px 0 20px;

  &__big {
    height: 375px;

    &-slider {
      &-item {
        height: 375px;
        padding: 45px 50px;

        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        border-radius: 5px;
      }

      &-title {
        margin-bottom: 11px;

        font-weight: 500;
        font-size: 32px;
        line-height: 37px;
        color: $black;
      }

      &-tx {
        margin-bottom: 25px;

        font-size: 16px;
        line-height: 140%;
        color: $black;
      }
    }
  }

  &__min {
    display: block;
    height: 177px;
    margin-bottom: 20px;

    padding: 30px 20px;

    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 5px;

    &-title {
      display: block;

      font-weight: 500;
      font-size: 18px;
      line-height: 130%;
      color: $black;
    }
  }
}

.slick {
  &-arrow {
    width: 35px;
    height: 35px;

    position: absolute;
    bottom: 40px;
    z-index: 3;

    background-color: rgba(255,255,255,0.25);
    border-radius: 100px;
    box-shadow: none;
    border: none;
    overflow: hidden;
    color: transparent;
    background-image: url("../images/svg/arrow.svg");
    background-repeat: no-repeat;
    background-size: 5px 9px;
    background-position: center;
    cursor: pointer;

    .banner & {
      background-color: rgba(255,255,255,0.25);
      border-radius: 100px;
      border: none;

      &:beefore {
        content: '';
        display: block;
        width: 5px;
        height: 9px;

        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;

        background-image: url("../images/svg/arrow.svg");
        background-repeat: no-repeat;
        background-size: 5px 9px;
        background-position: center;
      }
    }

    &:hover {
      background-color: $white;
    }
  }

  &-prev {
    left: 50px;
  }

  &-next {
    left: 95px;
    transform: rotate(180deg);
  }
}

.delivery {
  padding: 45px 0 95px;

  &-tw {
    padding: 100px 0;

    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    &__img {

    }

    &__info {
      padding-left: 60px;

      p {
        margin-bottom: 35px;

        font-size: 16px;
        line-height: 154%;
        color: $black4;
      }
    }

    &__title {
      margin-bottom: 27px;

      font-family: $font-family2;
      font-weight: 600;
      font-size: 30px;
      line-height: 39px;
      color: $black;
    }

    &__list {
      &-item {
        display: inline-flex;
        align-items: center;
        margin-right: 80px;
        margin-bottom: 20px;

        font-size: 14px;
        line-height: 16px;
        color: $black3;

        img {
          margin-right: 15px;
        }
      }
    }
  }

  &__info {
    padding: 0 50px;

    text-align: center;

    &-title {
      margin-top: 27px;
      margin-bottom: 20px;

      font-weight: 500;
      font-size: 18px;
      line-height: 130%;
      color: $black;
    }

    p {
      font-size: 16px;
      line-height: 154%;
      color: $black4;
    }
  }

  &__order {
    height: 100%;
    padding: 67px 115px 80px;

    border-radius: 5px;

    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    overflow: hidden;

    &-title {
      margin-bottom: 15px;

      font-weight: 500;
      font-size: 20px;
      line-height: 130%;
    }

    p {
      margin-bottom: 30px;

      font-size: 16px;
      line-height: 154%;
      color: $black4;
    }
  }
}

.bottom-block {
  background: $gray4;

  &__inner {
    display: flex;
  }

  &__title {
    margin-bottom: 25px;

    font-family: $font-family2;
    font-weight: 600;
    font-size: 26px;
    line-height: 32px;
    color: $black;
  }

  &__btns {
    &-item {
      display: inline-block;
      margin-bottom: 10px;
      margin-right: 10px;
    }
  }

  &__info {
    width: 40%;
    padding: 65px 0;
  }

  &__img {
    position: relative;

    &-tx {
      position: absolute;
      right: 47px;
      top: 101px;
      z-index: 2;

      font-size: 16px;
      line-height: 22px;
      color: $black3;
    }
  }
}

.footer {
  background: $gray4;
  border-top: 1px solid $bord;

  &__top {
    &-inner {
      padding: 22px 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }

  &__bottom {
    border-top: 1px solid $bord;

    &-inner {
      padding: 8px 0;

      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &-descr {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 312px;
    }

    &-info {
      margin-bottom: 0;

      li {
        display: inline-block;
        vertical-align: middle;
        margin-right: 22px;

        position: relative;

        font-size: 13px;
        line-height: 18px;
        color: $gray3;

        &:after {
          content: '';
          width: 3px;
          height: 3px;
          display: block;

          position: absolute;
          right: -15px;
          top: 0;
          bottom: 0;
          margin: auto;

          background: $gray3;
          border-radius: 10px;
        }

        &:last-of-type {
          &:after {
            display: none;
          }
        }

        a {
          color: $gray3;
          font-size: 13px;
          line-height: 18px;

          &:hover {
            color: $orange;
          }
        }
      }
    }
  }

  &__dev {
    display: inline-block;
    vertical-align: middle;

    font-size: 13px;
    line-height: 18px;
    color: $gray3;

    &:hover {
      color: $orange;
    }
  }

  &__soc {
    margin-bottom: 0;
    display: inline-block;
    vertical-align: middle;

    li {
      margin-right: 7px;
      display: inline-block;
      vertical-align: middle;

      a {
        svg {
          width: 22px;
          height: 22px;

          use {
            color: $black3;
          }
        }

        &:hover {
          svg {
            use {
              color: $orange;
            }
          }
        }
      }
    }
  }

  &__menu {
    margin-bottom: 0;

    li {
      display: inline-block;
      margin-right: 36px;

      a {
        letter-spacing: 0.07em;
        text-transform: uppercase;
        font-weight: 500;
        font-size: 13px;
        line-height: 121.03%;
        color: $black3;

        &:hover {
          color: $orange;
        }
      }
    }
  }

  &__tel,
  &__mail {
    margin-left: 46px;

    font-weight: 500;
    font-size: 14px;
    line-height: 121.03%;
    color: $black3;
    text-transform: uppercase;

    &:hover {
      color: $orange;
    }
  }
}

.catalog {
  margin: 50px 0 100px;

  &-min {
    margin-bottom: 25px;

    &-tw {
      margin-bottom: 85px;
    }

    &__top {
      margin-bottom: 20px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 9px 20px;

      background: $gray4;
      border-radius: 14px;

      &-title {
        font-weight: 500;
        font-size: 18px;
        line-height: 130%;
        color: $black;

        img {
          margin-right: 20px;
        }
      }
    }

    &__labels {
      margin-bottom: 10px;

      &-item {
        padding: 3px 21px;
        margin-right: 5px;
        margin-bottom: 10px;
        display: inline-block;
        vertical-align: middle;

        background: $gray2;
        border-radius: 10px;
        border: 1px solid transparent;

        span {
          font-size: 16px;
          line-height: 175%;
          color: $black4;
        }

        img {
          margin-right: 10px;
        }

        &:hover {
          border-color: $green;
          background: $white;
        }

      }
    }
  }

  &__item {
    height: 100%;
    padding-bottom: 90px;

    position: relative;

    border: 1px solid $bord;
    border-radius: 15px;
    background: $white;
    overflow: hidden;

    &-bt {
      padding-bottom: 120px;
    }

    &-top {
      position: relative;
    }

    &-bottom {
      width: 100%;

      position: absolute;
      left: 0;
      bottom: 0;
    }

    &-label {
      width: 43px;
      height: 43px;
      display: flex;
      align-items: center;
      justify-content: center;

      position: absolute;
      top: 8px;
      left: 11px;
      z-index: 3;

      border-radius: 100px;
      text-transform: uppercase;
      font-weight: 500;
      font-size: 12px;
      line-height: 14px;

      span {
        position: relative;
        z-index: 3;
      }

      &:before {
        content: '';
        width: 39px;
        height: 39px;
        display: block;

        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        z-index: 1;

        border: 1px solid transparent;
        border-radius: 100px;
      }
      &:after {
        content: '';
        width: 37px;
        height: 37px;
        display: block;

        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        z-index: 2;

        border: 1px solid transparent;
        border-radius: 100px;
      }

      &-hit {
        background: $gray6;
        color: $gray7;

        &:before {
          border-color: $gray9;
        }

        &:after {
          border-color: $gray8;
          background: $gray6;
        }
      }

      &-new {
        background: $green2;
        color: $green3;

        &:before {
          border-color: $green3;
